body, body ::backdrop {
  --sans-font: -apple-system, BlinkMacSystemFont, "Avenir Next", Avenir, "Nimbus Sans L", Roboto, "Noto Sans", "Segoe UI", Arial, Helvetica, "Helvetica Neue", sans-serif;
  --mono-font: Consolas, Menlo, Monaco, "Andale Mono", "Ubuntu Mono", monospace;
  --standard-border-radius: 5px;
  --bg: #fff;
  --accent-bg: #f5f5f5;
  --text: #212121;
  --text-light: #585858;
  --border: #8a8a8a;
  --accent: #4b4b4b;
  --accent-hover: #686868;
  --accent-text: var(--bg);
  --code: #696969;
  --preformatted: #444;
  --marked: #ccc;
  --disabled: #efefef;
}

@media (prefers-color-scheme: dark) {
  body, body ::backdrop {
    color-scheme: dark;
    --bg: #121212;
    --accent-bg: #1c1c1c;
    --text: #dcdcdc;
    --text-light: #ababab;
    --accent: #b5b5b5;
    --accent-hover: #d8d8d8;
    --accent-text: var(--bg);
    --code: #ccc;
    --preformatted: #ccc;
    --disabled: #111;
  }
}

.subtle {
  opacity: .8;
  font-style: italic;
}

body h1 {
  font-size: 2rem;
}

body h2 {
  font-size: 1.2rem;
}

ul {
  padding: unset;
  list-style-type: none;
}

ul .list-item button:before {
  content: " + ";
  margin-right: 1ch;
  font-weight: bold;
  display: inline-block;
}

ul.list-top .list-item button:before {
  content: " - ";
}

.list-item button, .list-item button:hover, .list-item button:focus {
  background-color: var(--bg);
  border-color: var(--border);
  color: var(--text);
  text-align: left;
  width: 100%;
  display: block;
}

li.list-item button:hover, .list-item button:focus {
  border-color: var(--text);
}

dialog {
  min-width: min(45rem, 90%);
}

.notice--assertive {
  --accent-bg: lightpink;
  --border: indianred;
}

.float-right {
  float: right;
}

body:has(dialog[open]) {
  overflow: hidden;
}
